<template>
  <div class="show">
    <div class="qr-code__icon iconfont icon-trade-assurance" @click="toggleShowQart">
      <br>
      <span class="qr-code__title">二维码</span>
    </div>
    <div class="scan__icon iconfont icon-trade-assurance" @click="toggleShowQrcodeReader">
      <br>
      <span class="scan__title">扫一扫</span>
    </div>
    <!-- <scan></scan> -->
    <div v-if="showQart" class="vue-q-art" @click="toggleShowQart">
      <vue-q-art class="vue-q-art" :config="config"></vue-q-art>
    </div>
    <div v-if="showQrcodeReader" class="qrcode-reader" @click="toggleShowQrcodeReader">
      <qrcode-reader @decode="onDecode" :paused="paused"></qrcode-reader>
    </div>
    <div class="welfare">
      <div class="welfare__name">薪资宝</div>
      <div class="welfare__desc">投资享受 + 1% 公司福利 不要错过</div>
      <span class="welfare__btn">合作吧</span>
    </div>
    <div class="func g__bg-color__border-top">
      <span class="func-icon iconfont icon-trade-assurance"></span>
      <a href="/" class="func-item">
        <p class="func-item__title">话费白拿</p>
        <p class="func-item__desc">终身减免话费</p>
      </a>
      <a class="func-item">
      </a>
    </div>
    <div class="func">
      <span class="func-icon iconfont icon-trade-assurance"></span>
      <a href="/" class="func-item">
        <p class="func-item__title">物业宝</p>
        <p class="func-item__desc">暂未开放</p>
      </a>
      <a href="/" class="func-item">
        <p class="func-item__title">物业减免</p>
        <p class="func-item__desc">暂未开放</p>
      </a>
    </div>
    <div class="func">
      <span class="func-icon iconfont icon-trade-assurance"></span>
      <a href="/" class="func-item">
        <p class="func-item__title">停车宝</p>
        <p class="func-item__desc">暂未开放</p>
      </a>
      <a href="/" class="func-item">
        <p class="func-item__title">洗车减免</p>
        <p class="func-item__desc">暂未开放</p>
      </a>
    </div>
    <img class="g__bg-color__border-top" src="../assets/logo.jpeg" alt="" style="width: 100%;">
    <myfooter class="global_footer"></myfooter>
  </div>
</template>

<script>
import VueQArt from 'vue-qart'
import myfooter from './footer.vue'
import logoImg from '../assets/logo.jpeg'
import { QrcodeReader } from 'vue-qrcode-reader'
import { startsWith } from 'lodash'

export default {
  data () {
    return {
      showQart: false,
      config: {
        value: 'http://www.58bjcf.com/baijia/login.html',
        imagePath: logoImg,
        filter: 'color'
      },
      // title: '分享',
      showQrcodeReader: false,
      paused: false
    }
  },
  methods: {
    toggleShowQart () {
      this.showQart = !this.showQart
    },
    toggleShowQrcodeReader () {
      this.showQrcodeReader = !this.showQrcodeReader
    },
    onDecode (content) {
      this.paused = true
      // alert(content)
      content && console.log(content)
      if (startsWith(content, 'http://') || startsWith(content, 'https://')) {
        location.href = content
      }
    }
  },
  components: {
    QrcodeReader,
    VueQArt,
    myfooter
  }
}
</script>

<style scoped>
.qr-code__icon, .scan__icon {
  margin: -5px;
  padding: 3rem 3rem 1rem;
  font-size: 4rem;
  line-height: 2rem;
  display: inline-block;
  background-color: rgb(17, 138, 255);
  color: white;
  min-width: 51%;
  box-sizing: border-box;
}
.qr-code__title, .scan__title {
  font-size: 1rem;
  color: white;
}
.vue-q-art {
  width: 100%;
  height: 100%;
  z-index: 1;
  position: fixed;
  top: 0;
  background-color: white;
  padding-top: 40%;
}
.qrcode-reader {
  width: 100%;
  height: 100%;
  z-index: 1;
  position: fixed;
  top: 0;
  background-color: black;
  padding-top: 40%;
}
.welfare {
  padding: 1rem;
}
.welfare__name {
  font-size: 1.5rem;
}
.welfare__desc {
  font-size: 0.75rem;
  line-height: 2rem;
  color: #9E9E9E;
}
.welfare__btn {
  font-size: .75rem;
  background-color: rgb(17, 138, 255);
  color: white;
  padding: .2rem 0.6rem;
  border-radius: 0.2rem;
}
.func {
  border-top: 1px solid #e2dede;
}
.func-icon, .func-item {
  width: 33.3%;
}
.func-icon {
  font-size: 3rem;
  border-right: 1px solid #e2dede;
  padding-right: 1rem;
}
.func-item {
  display: inline-block;
}
.func-item__title {
  margin-bottom: 0;
}
.func-item__desc {
  font-size: .6rem;
  color: #9E9E9E;
  margin-top: 0;
}
</style>



