<template>
  <div class="person">
    <div class="back-img">
      <router-link :to="toPersonInfo">
        <img class="head-portrait" :src="user.head" alt="">
        <span class="nickname">{{ user.nickname }}</span>
        <span class="member-level" v-if="user.is_diamond === 1">钻石会员{{ user.stock }}股</span>
        <span class="member-level" v-else-if="user.level === 0">待激活</span>
        <span class="member-level" v-else-if="user.level === 1">铜牌合伙人</span>
        <span class="member-level" v-else-if="user.level === 2">银牌合伙人</span>
        <span class="member-level" v-else-if="user.level === 3">金牌合伙人</span>
      </router-link>
      <br>
      <div class="sign-in__icon iconfont icon-trade-assurance">
        <span class="sign-in__title"> 签到</span>
      </div>
      <div class="bank-card__icon iconfont icon-trade-assurance">
        <span class="bank-card__title"> 银行卡</span>
      </div>
    </div>
    <a href="/" class="func iconfont icon-trade-assurance">
      <br>
      <span class="func__title">资料</span>
    </a>
    <router-link :to="toWallet" class="func iconfont icon-trade-assurance">
      <br>
      <span class="func__title">钱包</span>
    </router-link>
    <a href="/message" class="func iconfont icon-trade-assurance">
      <br>
      <span class="func__title">消息</span>
    </a>
    <a href="/upgrade" class="func iconfont icon-trade-assurance">
      <br>
      <span class="func__title">升级</span>
    </a>
    <a href="/" class="func iconfont icon-trade-assurance">
      <br>
      <span class="func__title">会员</span>
    </a>
    <a href="/" class="func iconfont icon-trade-assurance">
      <br>
      <span class="func__title">课堂</span>
    </a>
    <a href="/" class="func iconfont icon-trade-assurance">
      <br>
      <span class="func__title">便民</span>
    </a>
    <a class="func"></a>
    <a class="func"></a>
    <myfooter class="global_footer"></myfooter>
  </div>
</template>

<script>
import axios from 'axios'
import myfooter from './footer.vue'

export default {
  data () {
    return {
      user: {},
      toPersonInfo: {
        name: 'personInfo',
        params: {}
      },
      toWallet: {
        name: 'wallet',
        params: {}
      }
    }
  },
  mounted () {
    this.getUserData()
  },
  methods: {
    getUserData () {
      axios.get('/baijia/userInfo')
        .then((response) => {
          this.user = response.data.user
          var data = {
            'login_status': 1,
            'user': {
              'id': 81971,
              'name': null,
              'tel': '13732026286',
              'wechat': null,
              'address': null,
              'created_at': '2017-12-27 15:37:42',
              'updated_at': '2018-01-24 16:51:08',
              'head': 'http://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoBBZtfNuRuK02e6QugKv7BN3CGQibn7anicLQmuq9pRPSlMJP1jQlibJFTUu4J8yqNKxJxticO8LD1Lg/0',
              'nickname': '\u9752',
              'balance': 0,
              'withdraw': 0,
              'review': 0,
              'total': 0,
              'level': 0,
              'pid': 80035,
              'is_diamond': 0,
              'count1': 0,
              'count2': 0,
              'count3': 0,
              'has_pay': 0,
              'has_cal_pid': 0,
              'stock': 0,
              'deleted_at': null,
              'top_id': 80035,
              'alipay': null,
              'red': 1,
              'code': 24,
              'old': 0
            },
            'followed': true,
            'message_count': 0
          }
          this.user = data.user
          this.toPersonInfo.params = data.user
          this.toWallet.params = data.user
          // console.log(response.data)
          // console.log(this.user)
          // alert(this.user.head)
          // this.userData.head = axios.defaults.baseURL + this.userData.head
        })
    }
  },
  components: {
    myfooter
  }
}
</script>

<style scoped>
.back-img {
  background-color: #2196f385;
  height: 10rem;
}
.head-portrait {
  height: 4rem;
  border-radius: 50%;
  position: relative;
  left: -4rem;
  top: 2rem;
}
.nickname {
  position: relative;
  left: -4rem;
  top: -1rem;
}
.member-level {
  position: relative;
  left: -5rem;
  top: 1rem;
}
.sign-in__icon, .bank-card__icon {
  font-size: 2rem;
  display: inline-block;
  background-color: white;
  margin-right: -5px;
  position: relative;
  top: 42px;
}
.sign-in__icon {
  padding: 1rem 2rem 0 4rem;
  border-radius: 1rem 0 0 0;
}
.bank-card__icon {
  padding: 1rem 4rem 0 2rem;
  border-radius: 0 1rem 0 0;
}
.sign-in__title, .bank-card__title {
  font-size: 1rem;
  vertical-align: middle;
}
.func {
  width: 32.5%;
  display: inline-block;
  font-size: 3rem;
  line-height: 1.5rem;
  margin-top: 2.5rem;
}
.func__title {
  font-size: 1rem;
}
</style>



