<template>
  <div class="person-info">
    <div class="info-item" v-for="(item, index) in showInfo" :key="index">
      <span class="iconfont" :class="item.icon"></span>
      <span class="info-text">{{ item.text }}</span>
      <span class="info-val">{{ item.val }}</span>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      title: 'qq',
      personInfo: {},
      showInfo: {
        tel: {
          icon: 'icon-trade-assurance',
          text: '手机号码',
          val: ''
        },
        nickname: {
          icon: 'icon-trade-assurance',
          text: '昵称',
          val: ''
        },
        alipay: {
          icon: 'icon-trade-assurance',
          text: '支付宝',
          val: ''
        }
      }
    }
  },
  mounted () {
    this.personInfo = this.$route.params
    this.showInfo.tel.val = this.$route.params.tel
    this.showInfo.nickname.val = this.$route.params.nickname
    this.showInfo.alipay.val = this.$route.params.alipay || ''
    console.log(this.showInfo)
  }
}
</script>

<style scoped>
.info-item {
  line-height: 2rem;
  border-bottom: 1px solid #e2dcdc;
  margin: 16px;
  display: flex;
  justify-content: center;
}
.iconfont {
  font-size: 1.5rem;
  margin-left: 1rem;
}
.info-text {
  margin-left: 1rem;
  margin-right: auto;
}
.info-val {
  margin-right: 1rem;
}
</style>



