<template>
  <div class="message">
    <span class="message_info" @click="show = true" :style="{borderBottom: show ? '4px solid' : ''}">消息</span>
    <span class="message_notify" @click="show = false" :style="{borderBottom: !show ? '4px solid' : ''}">通知</span>
    <hr style="margin-top: -2px;">
    <div v-show="show">
      <div v-for="item in messageData" :key="item.id">
        <p>{{ item.title}}</p>
        <p>{{ item.date}}</p>
      </div>
    </div>
    <div v-show="!show">22</div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      title: '',
      show: true,
      messageData: [
        {title: '11', date: '111', id: 1},
        {title: '222', date: '22', id: 2}
      ]
    }
  },
  mounted () {
    this.getMessage()
  },
  methods: {
    getMessage () {
      axios.get('/baijia/messages')
        .then((response) => {
          console.log(response.data.data)
          this.data = response.data.data
        })
    }
  }
}
</script>

<style>
.message_info, .message_notify {
  display: inline-block;
  width: 49%;
  line-height: 3rem;
  /*border-bottom: 1px solid;*/
}
</style>



