<template>
  <div class="classroom">
    <img src="../assets/logo.jpeg" style="width:100%;" alt="">
    <router-link to="ArticleDetail" class="knowledge__subject g__bg-color__border-top">知识课堂</router-link>
<!--     <router-link v-for="(knowledgeItem, index) in knowledge" :key="index">
      {{ knowledgeItem.type }}
      {{ knowledgeItem.title }}
    </router-link> -->
    <router-link
      class="knowledge-item"
      :to="{path: '/'}"
      v-for="knowledgeItem in knowledge"
      :key="knowledgeItem.id">
      <img class="knowledge-item__img" :src="knowledgeItem.pic" alt="">
      <p>{{ knowledgeItem.title }}</p>
      <p>{{ knowledgeItem.desc }}</p>
      <p class="knowledge-item__time">{{ knowledgeItem.updated_at }}<span class="label">知识</span></p>
    </router-link>
<!--     <div v-for="(experienceItem, index) in experience" :key="index">
      {{ experienceItem.type }}
      {{ experienceItem.title }}
    </div> -->
  </div>
</template>

<script>
import axios from 'axios'
import _ from 'lodash'

export default {
  data () {
    return {
      title: '',
      knowledge: [],
      experience: []
    }
  },
  mounted () {
    this.getNews()
  },
  methods: {
    getNews () {
      axios.get('/baijia/news')
        .then((response) => {
          // console.log(response.data)
          let data = _.chain(response.data.data)
          console.log(data)
          this.knowledge = _.chain(data)
            .filter((item) => {
              return item.type === 1
            })
            .map((item) => {
              item.pic = axios.defaults.baseURL + item.pic
              return item
            })
            .sampleSize(3)
            .value()
          // this.experience = _.chain(data)
          //   .filter((item) => {
          //     return item.type === 1
          //   })
          //   .map((item) => {
          //     item.pic = axios.defaults.baseURL + item.pic
          //     return item
          //   })
          //   .sampleSize(3)
          //   .value()
          console.log(this.knowledge)
        })
    }
  }
}
</script>

<style>
.knowledge__subject {
  display: block;
  line-height: 2.5rem;
}
.knowledge-item {
  text-align: left;
  padding-top: 8px;
  padding-left: 1rem;
  display: block;
  padding-right: 1rem;
}
.knowledge-item__img {
  float: left;
  height: 50px;
  padding: 2px 8px 8px 0;
}
.knowledge-item__time {
  color: #aaa;
  display: flex;
  justify-content: space-between;
}
.knowledge-item p {
  margin: 0;
  line-height: 20px;
  font-size: 10px;
  overflow: hidden;/*内容超出后隐藏*/
  text-overflow: ellipsis;/* 超出内容显示为省略号*/
  white-space: nowrap;/*文本不进行换行*/
}
.label {
  border: 1px solid;
  border-radius: .3rem;
  padding: 0 .3rem;
}
</style>



