<template>
  <div class="login">
    <img class="logo" src="../assets/logo.jpeg" alt="">
    <p class="project_name">58财富</p>
    <input v-if="login_by == 'username'" class="username line" type="text" placeholder="用户名">
    <input v-if="login_by == 'phoneNumber'" v-model="phoneNumber" class="username line" type="tel" placeholder="手机号码">
    <br>
    <input v-if="login_by == 'username'" class="password line" type="password" placeholder="密码">
    <input v-if="login_by == 'phoneNumber'" v-model="authCode" class="password line" type="text" placeholder="验证码">
    <a class="login_btn skin_background_color" @click="login">登录</a>
    <br>
    <a v-if="login_by == 'username'" class="register" href="">注册</a>
    <a v-if="login_by == 'username'" class="forget_password" href="">忘记密码</a>
    <hr class="line login_phone-line">
    <a class="login_by_other" @click="loginByOther">{{ login_by == 'phoneNumber'?'用户名登录':'手机号登录'}}</a>
  </div>
</template>

<script>
import axios from 'axios'
import _ from 'lodash'

export default {
  name: 'login',
  data () {
    return {
      login_by: 'phoneNumber',
      phoneNumber: '',
      authCode: ''
    }
  },
  watch: {
    phoneNumber (newNum, oldNum) {
      this.getSms(newNum)
    },

    authCode (newCode, oldCode) {
      if (newCode.length === 4) this.login()
    }
  },
  methods: {
    getSms: _.debounce(
      function (newNum) {
        if (this.isPhoneNum(newNum)) {
          console.log(newNum)
          this.sendSms(newNum)
        } else {
          console.log('请输入正确的手机号码!')
        }
      },
      2000
    ),
    loginByOther () {
      this.login_by = this.login_by === 'phoneNumber' ? 'username' : 'phoneNumber'
    },
    isPhoneNum (PhoneNum) {
      let phone = /^0?1[3|4|5|8][0-9]\d{8}$/
      return phone.test(PhoneNum)
    },
    sendSms (PhoneNum) {
      console.log('验证码发送中.....')
      axios.get('/baijia/sendSmsCode', {
        params: {
          tel: PhoneNum
        }
      })
        .then((response) => {
          if (response.data.rs === 1) {
            console.log('验证码发送成功！请注意查收')
          } else {
            console.log(response.data.msg)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    login () {
      axios.get('/baijia/doLogin', {
        params: {
          tel: this.phoneNumber,
          code: this.authCode
        }
      })
        .then((response) => {
          console.log(response)
          if (response.data.rs) {
            location.href = 'main'
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}

</script>

<style scoped>
.logo {
  height: 100px;
  border-radius: 50%;
  margin-top: 50px;
}
.project_name {
  text-align: center;
  font-weight: 900;
  font-size: 24px;
  margin: 0;
}
.line {
  line-height: 60px;
  width: 80%;
  font-size: 16px;
  border-style: none;
  border-bottom: 1px solid #b2d6f3;
}
.login_btn {
  width: 70%;
  border-radius: 5px;
  line-height: 240%;
  margin-top: 50px;
  font-size: 16px;
  margin-bottom: 16px;
  display: inline-block;
}
.register {
  margin-right: 25%;
}
.forget_password {
  margin-left: 25%;
}
.login_phone-line {
  position: absolute;
  bottom: 60px;
  left: 50%;
  margin-left: -40%;
}
.login_by_other {
  position: fixed;
  bottom: 30px;
  left: 50%;
  margin-left: -48px;
  font-size: 16px;

}
</style>



