<template>
  <div class="main">
    <banner class="main__banner" :swiperSlides = 'banners'></banner>
    <div class="notice">
      <span class="iconfont icon-remind"></span>
      <span class="notice_centent">{{ notice.centent }}</span>
      <span class="notice_time">{{ notice.time }}</span>
      <!-- {{ news }} -->
    </div>
    <main-action-option></main-action-option>
    <recommend class="g__bg-color__border-top" :recommendDatas = 'news'></recommend>
    <welfare class="welfare g__bg-color__border-top"></welfare>
    <myfooter class="global_footer"></myfooter>
  </div>
</template>

<script>
import axios from 'axios'
import _ from 'lodash'
import banner from './Banner.vue'
import mainActionOption from './mainActionOption.vue'
import recommend from './recommend.vue'
import welfare from './welfare.vue'
import myfooter from './footer.vue'

export default {
  data () {
    return {
      title: '首页',
      notice: {
        centent: '这里写公告内容.....',
        time: '2017-01-01'
      },
      news: [],
      banners: []
    }
  },
  mounted () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      this.getBanner()
      this.getNews()
    },
    getNews () {
      axios.get('/baijia/news', {
        params: {
          type: 1,
          page: 1
        }
      })
        .then((response) => {
          this.news = _.chain(response.data.data)
            .sampleSize(3)
            .map((item) => {
              item.pic = axios.defaults.baseURL + item.pic
              return _.mapValues(item, (v, i) => {
                return v || '...'
              })
            })
            .value()
        })
    },
    getBanner () {
      axios.get('/baijia/banner')
        .then((response) => {
          // console.log(response)
          this.banners = _.map(response.data, (item) => {
            item.pic = axios.defaults.baseURL + item.pic
            return item
          })
        })
    }
  },
  components: {
    banner,
    mainActionOption,
    recommend,
    welfare,
    myfooter
  }
}
</script>

<style scoped>
.main__banner {
  height: 12rem;
}
.iconfont {
  font-size: 20px;
  padding-left: 8px;
}
.notice {
  display: flex;
  justify-content: space-around;
  line-height: 30px;
}
.notice_centent {
  font-size: 14px;
  flex-grow: 2;
  text-align: left;
  padding-left: 8px;
}
.notice_time {
  font-size: 14px;
  color: #9e9e9e;
  padding-right: 8px;
}
.recommend {
  border-top: 16px solid rgb(245, 245, 245);
}
</style>



