<template>
  <div class="article-detail">
    {{ infoData.content}}
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      content: '',
      infoData: ''
    }
  },
  mounted () {
    // console.log(id)
    this.getData()
  },
  methods: {
    getData () {
      const id = this.$route.query.id
      axios.get('/baijia/newsDetail', {
        params: {
          id: id
        }
      })
        .then((response) => {
          this.infoData = response.data.data
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}
</script>



