<template>
  <div class="wallet">
    <div class="info-item" v-for="(item, index) in showInfo" :key="index">
      <span class="iconfont" :class="item.icon"></span>
      <span class="info-text">{{ item.text }}</span>
      <span class="info-val" v-html="item.val"></span>
    </div>
    <button class="withdraw__btn">我要提现</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      title: '',
      showInfo: {
        balance: {
          icon: 'icon-trade-assurance',
          text: '我的余额',
          val: ''
        },
        nickname: {
          icon: 'icon-trade-assurance',
          text: '提现记录',
          val: '<span class="iconfont icon-more"></span>'
        },
        alipay: {
          icon: 'icon-trade-assurance',
          text: '充值明细',
          val: '<span class="iconfont icon-more"></span>'
        },
        alipay1: {
          icon: 'icon-trade-assurance',
          text: '审核中',
          val: '<span class="iconfont icon-more"></span>'
        }
      }
    }
  },
  mounted () {
    this.showInfo.balance.val = this.$route.params.balance
  }
}
</script>

<style scoped>
.info-item {
  line-height: 2rem;
  border-bottom: 1px solid #e2dcdc;
  margin: 16px;
  display: flex;
  justify-content: center;
}
.iconfont {
  font-size: 1.5rem;
  margin-left: 1rem;
}
.info-text {
  margin-left: 1rem;
  margin-right: auto;
}
.info-val {
  margin-right: 1rem;
}
.withdraw__btn {
  background-color: rgb(17, 138, 255);
  color: white;
  line-height: 2rem;
  width: 80%;
  margin: 1rem;
  border-radius: .3rem;
}
</style>



