<template>
  <div class="upgrade">
    <span>当前:</span><span>普通会员</span>
    <p class="text-huiyuandengji">会员等级</p>
    <hr>
    <div class="grade-select">
      <span class="grade-select__item">￥99/铜牌</span>
      <span class="grade-select__item">￥299/银牌</span>
      <span class="grade-select__item">￥599/金牌</span>
    </div>
    <button class="pay-btn">开通并支付</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      title: '会员升级'
    }
  }
}
</script>

<style scoped>
.text-huiyuandengji {
  text-align: left;
  margin: .5rem 2rem;
}
.grade-select {
  display: flex;
  justify-content: space-around;
  line-height: 2rem;
}
.grade-select__item {
  border: 1px solid;
  padding: 0.1rem 1rem;
  margin-top: 1rem;
  border-radius: .3rem;
}
.pay-btn {
  background-color: rgb(17, 138, 255);
  color: white;
  line-height: 2rem;
  width: 80%;
  margin: 1rem;
  border-radius: .3rem;
}
</style>



