import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import login from '@/components/login'
import main from '@/components/main'
import netloan from '@/components/netloan'
import person from '@/components/person'
import personInfo from '@/components/personInfo'
import show from '@/components/show'
import ArticleDetail from '@/components/ArticleDetail'
import upgrade from '@/components/upgrade'
import classroom from '@/components/classroom'
import message from '@/components/message'
import wallet from '@/components/wallet'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      // name: '/',
      // component: main
      redirect: '/main'
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/main',
      name: 'main',
      component: main
    },
    {
      path: '/netloan',
      name: 'netloan',
      component: netloan
    },
    {
      path: '/person',
      name: 'person',
      component: person
    },
    {
      path: '/personInfo',
      name: 'personInfo',
      component: personInfo
    },
    {
      path: '/show',
      name: 'show',
      component: show
    },
    {
      path: '/ArticleDetail',
      name: 'ArticleDetail',
      component: ArticleDetail
    },
    {
      path: '/upgrade',
      name: 'upgrade',
      component: upgrade
    },
    {
      path: '/classroom',
      name: 'classroom',
      component: classroom
    },
    {
      path: '/message',
      name: 'message',
      component: message
    },
    {
      path: '/wallet',
      name: 'wallet',
      component: wallet
    }

  ]
})



