<template>
  <div class="netloan">
    <img src="../assets/logo.jpeg" alt="" style="height: 100px;">
    <ul class="netloan-filter">
      <li @click="changeNetLoanFilterOption('loanType')" class="netloan-filter__option">贷款种类</li>
      <li @click="changeNetLoanFilterOption('maxAmount')" class="netloan-filter__option">最高额度</li>
      <li @click="changeNetLoanFilterOption('isCredit')" class="netloan-filter__option">是否上征信</li>
    </ul>
    <ul class="loan-type" v-show="netloanFilter === 'loanType'">
      <li class="loan-type__one" @click="filterLoanType(1)">身份证贷</li>
      <li class="loan-type__one" @click="filterLoanType(3)">信用卡贷</li>
      <li class="loan-type__one" @click="filterLoanType(4)">公积金贷</li>
      <li class="loan-type__one" @click="filterLoanType(6)">黑户贷</li>
      <li class="loan-type__one" @click="filterLoanType(7)">按揭房贷</li>
      <li class="loan-type__one" @click="filterLoanType(11)">保单贷</li>
    </ul>
    <ul class="max-amount" v-show="netloanFilter === 'maxAmount'">
      <li class="max-amount__scope" @click="filterAmount(1)">1000元以下</li>
      <li class="max-amount__scope" @click="filterAmount(2)">1000-10000</li>
      <li class="max-amount__scope" @click="filterAmount(3)">10000以上</li>
    </ul>
    <ul class="is-credit" v-show="netloanFilter === 'isCredit'">
      <li class="is-credit__one" @click="filterIsCredit(1)">查征信</li>
      <li class="is-credit__one" @click="filterIsCredit(2)">不查征信</li>
    </ul>
    <hr>
    <ul class="netloan-result">
      <li class="netloan-result__one" v-for="(oneLoan, index) in showData" :key="index" type="a" @click="go(oneLoan.url)">
        <img class="netloan-result__img" :src="oneLoan.pic" alt="">
        <h3 class="netloan-result__title">{{ oneLoan.title }}</h3>
        <p class="netloan-result__max-amount">最高额度:{{ oneLoan.zged | zeroTok }}</p>
        <p class="netloan-result__loan-number">放款人数:{{ oneLoan.clicks }}</p>
      </li>
    </ul>
    <myfooter class="global_footer"></myfooter>
  </div>
</template>

<script>
import axios from 'axios'
import _ from 'lodash'
import myfooter from './footer.vue'

export default {
  name: 'netloan',
  data () {
    return {
      netloanFilter: 'loanType',
      loanDataStore: [],
      showData: []
    }
  },
  mounted () {
    this.getLoanData()
  },
  filters: {
    zeroTok (num) {
      num = num >= 10000 ? num / 1000 + 'k' : num
      return num
    }
  },
  methods: {
    changeNetLoanFilterOption (netloanFilterOption) {
      this.netloanFilter = netloanFilterOption
    },
    getLoanData () {
      axios.get('/baijia/loanCenterAjax')
        .then((response) => {
          // console.log(response)
          if (response.data.errcode === 0) {
            this.loanDataStore = _.map(response.data.errdata, (item) => {
              item.pic = axios.defaults.baseURL + item.pic
              return item
            })
            this.showData = this.loanDataStore
          }
          // console.log(this.loanDataStore)
        })
    },
    filterAmount (type) {
      switch (type) {
        case 1:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.zged <= 1000
          })
          break
        case 2:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.zged > 1000 && item.zged < 10000
          })
          break
        case 3:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.zged >= 10000
          })
          break
      }
    },
    filterLoanType (type) {
      switch (type) {
        case 1:
          this.showData = _.filter(this.loanDataStore, (item) => {
            // console.log(item.type)
            return item.type === 1
          })
          break
        case 2:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.type === 2
          })
          break
        case 3:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.type === 3
          })
          break
        case 4:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.type === 4
          })
          break
        case 5:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.type === 5
          })
          break
        case 6:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.type === 6
          })
          break
        case 7:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.type === 7
          })
          break
        case 11:
          this.showData = _.filter(this.loanDataStore, (item) => {
            return item.type === 11
          })
          break
      }
    },
    filterIsCredit (type) {
      this.showData = _.filter(this.loanDataStore, (item) => {
        return item.is_cha === type
      })
    },
    go (url) {
      location.href = url
    }
  },
  components: {
    myfooter
  }
}
</script>

<style scoped>
.netloan-filter {
  display: flex;
  justify-content: space-around;
  color: #aaa;
}
.loan-type, .max-amount, .is-credit {
  display: flex;
  justify-content: space-around;
  color: #aaa;
}
.loan-type {
  flex-wrap: wrap;
  padding: 0px 4px;
}
.loan-type__one, .max-amount__scope, .is-credit__one {
  line-height: 30px;
  /*width: 31%;*/
  border-radius: 3px;
  margin: 5px 4px;
  background-color: #aaa;
  color: white;
  /*flex-grow: 1;*/
  flex: 0 0 30%;
}
.netloan-result {
  display: flex;
  flex-wrap: wrap;
}
.netloan-result__one {
  background-color: #ddd;
  margin: 2%;
  padding: 2%;
  border-radius: 8px;
  flex: 0 0 25%;
}
.netloan-result__img {
  height: 50px;
  border-radius: 8px;
}
.netloan-result__title {
  margin: 0;
  font-size: 14px;
}
.netloan-result__max-amount {
  margin: 0;
  font-size: 12px;
}
.netloan-result__loan-number {
  margin: 0;
  font-size: 12px;
}
</style>



