<template>
  <div class="recommend">
    <a class="recommend__title" href="/show">
      <span>热门推荐</span>
      <span class="recommend__more">
        更多
        <span class="iconfont icon-more"></span>
      </span>
    </a>
    <router-link :to="{ path : '/ArticleDetail', query : { id : recommendData.id }}" class="recommendRecord" v-for="(recommendData, index) in recommendDatas" :key="index">
      <img class="recommendRecord__img" :src="recommendData.pic" alt="">
      <!-- <img class="recommendRecord__img" src="../assets/logo.jpeg" width="50" alt=""> -->
      <p>{{ recommendData.title }}</p>
      <p>{{ recommendData.desc }}</p>
      <p class="recommendRecord__time">{{ recommendData.updated_at }}</p>
    </router-link>
  </div>
</template>

<script>
export default {
  data () {
    return {
      a: []
    }
  },
  props: ['recommendDatas']
}
</script>

<style>
@import '../assets/font/iconfont.css'
</style>

<style scoped>
.recommend__title {
  display: flex;
  justify-content: space-between;
  padding: 0 16px;
  line-height: 32px;
}
.recommendRecord {
  border-top: 1px solid #ddd;
  text-align: left;
  padding-top: 8px;
  padding-left: 8px;
  display: block;
  padding-right: 8px;
}
.recommend__more {
  color: #aaa;
}
.iconfont {
  color: #aaa;
}
.recommendRecord__img {
  float: left;
  height: 50px;
  width: 50px;
  padding: 2px 8px 8px 0;
}
.recommendRecord__time {
  color: #aaa;
}
.recommendRecord p {
  margin: 0;
  line-height: 20px;
  font-size: 10px;
  overflow: hidden;/*内容超出后隐藏*/
  text-overflow: ellipsis;/* 超出内容显示为省略号*/
  white-space: nowrap;/*文本不进行换行*/
}
</style>



